@charset "UTF-8";
body {
  margin: 0;
}

.rk-container {
  margin-right: auto;
  margin-left: auto;
  padding-left: .25rem;
  padding-right: .25rem;
}
@media (min-width: 40em) {
  .rk-container {
    max-width: 88.88889em;
    width: 95vw;
  }
  .rk-container--inner {
    max-width: 68.88889em;
    width: 82vw;
  }
}
.rk-container--full {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
  width: 100%;
}
@media (min-width: 64em) {
  .rk-container {
    padding-left: 0;
    padding-right: 0;
  }
}

.rk-main {
  z-index: 1;
  position: static;
}
.top-fixed .rk-main {
  padding-top: 8.5rem;
}
@media (min-width: 64em) {
  .rk-main {
    position: relative;
  }
}

.rk-header {
  background-color: #fff;
  position: relative;
  z-index: 5;
}
.top-fixed .rk-header {
  position: fixed;
}

.rk-topbar {
  align-items: flex-start;
  display:         flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 3.3rem;
  padding-top: 2.8rem;
}
@media (min-width: 64em) {
  .rk-topbar {
    padding-bottom: 0;
  }
}

.rk-logo {
  flex: 0 0 100%;
  font-size: 1.375rem;
  font-weight: 700;
  letter-spacing: 2.25px;
  text-align: center;
  text-transform: uppercase;
  width: 20%;
}
.rk-logo sup {
  font-size: .5625rem;
  position: relative;
  top: -1px;
}
@media (min-width: 64em) {
  .rk-logo {
    flex: 1 1;
    text-align: left;
  }
}

.rk-search {
  align-self: flex-start;
  margin-bottom: 3.4rem;
  margin-left: 7.7rem;
  padding-top: .16rem;
  position: relative;
}
.rk-search label,
.rk-search input {
  display: inline-block;
  vertical-align: middle;
}
.rk-search label {
  cursor: pointer;
  height: 1.875rem;
  left: -1.5rem;
  padding-bottom: .67em;
  padding-top: 0;
  position: absolute;
  top: 0;
  transition: color .3s;
  width: 1.875rem;
  z-index: 20;
}
.rk-search svg {
  height: 100%;
  width: 100%;
}
.rk-search input {
  background-color: transparent;
  border: 0;
  font-size: .875rem;
  opacity: 0;
  outline: 0;
  padding: 0;
  padding-bottom: .3em;
  transition: .3s;
  z-index: 10;
  width: 1px;
}
.rk-search input:focus {
  opacity: 1;
  padding-left: .5rem;
  width: 100px;
}
.rk-search input:focus + label {
  color: #a0a0a0;
}

.rk-navmobile {
  position: relative;
}

.rk-mobile-menu {
  display: none;
}
.rk-mobile-menu + label {
  cursor: pointer;
  display: inline-block;
  height: 24px;
  left: 2.5vw;
  position: absolute;
  top: 2.7rem;
  width: 16px;
  z-index: 300;
}
.rk-mobile-menu + label svg {
  height: 4px;
  width: 20px;
  margin: 0;
  display: block;
  margin-bottom: 3px;
  transition: .3s;
}
.rk-mobile-menu + label svg:nth-of-type(1) {
  -webkit-transform-origin: center left;
          transform-origin: center left;
}
.rk-mobile-menu + label svg:nth-of-type(3) {
  -webkit-transform-origin: center left;
          transform-origin: center left;
}
@media (min-width: 64em) {
  .rk-mobile-menu + label {
    display: none;
  }
}
.rk-mobile-menu:checked + label,
.rk-mobile-menu + label:hover {
  color: #a0a0a0;
}
.rk-mobile-menu:checked + label svg:nth-of-type(1) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.rk-mobile-menu:checked + label svg:nth-of-type(2) {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
}
.rk-mobile-menu:checked + label svg:nth-of-type(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.rk-footer {
  margin-top: 10.5rem;
  margin-bottom: 2rem;
}

.rk-footer__text {
  color: #787878;
  font-size: .75rem;
  margin-bottom: .875rem;
}
.rk-footer__text span {
  font-size: .875rem;
}

.rk-footer__copy {
  letter-spacing: .4px;
}
.rk-footer__copy span {
  letter-spacing: 1.2px;
}

.rk-footer__contact {
  display: inline-block;
  letter-spacing: .6px;
}

.rk-footer__by {
  letter-spacing: .7px;
}

.rk-social-btn {
  background: none;
  border: 0;
  display: inline-block;
  height: 20px;
  margin-left: 1rem;
  margin-right: 1rem;
  width: 20px;
}
.rk-social-btn:first-child {
  margin-left: 0;
}
.rk-social-btn:last-child {
  margin-right: 0;
}
.rk-social-btn svg {
  height: 100%;
  width: 100%;
}

.rk-layout-ctrl-cont {
  position: absolute;
  text-align: right;
  top: 2.65rem;
  padding-right: 2.5rem;
}
.top-fixed .rk-layout-ctrl-cont {
  position: fixed;
  z-index: 401;
}
.top-fixed .rk-layout-ctrl-cont:first-of-type {
  top: 2.65rem;
}
@media (min-width: 64em) {
  .rk-layout-ctrl-cont {
    padding-right: 0;
    top: 2.25rem;
  }
  .rk-layout-ctrl-cont:first-of-type {
    top: 0;
  }
  .top-fixed .rk-layout-ctrl-cont {
    top: 10.7625rem;
    z-index: -1;
  }
  .top-fixed .rk-layout-ctrl-cont:first-of-type {
    top: 8.5rem;
  }
}

.rk-layout-ctrl {
  cursor: pointer;
  display: none;
  float: right;
  clear: both;
  height: 28px;
  opacity: 0;
  padding-right: .09rem;
  padding-top: 0;
  transition: color .3s;
  width: 20px;
  z-index: 401;
  position: absolute;
}
.rk-layout-ctrl:hover {
  color: #a0a0a0;
}
.rk-layout-ctrl svg {
  height: 100%;
  width: 100%;
}
@media (min-width: 40em) {
  .rk-layout-ctrl {
    display: inline-block;
  }
}
@media (min-width: 64em) {
  .rk-layout-ctrl {
    opacity: 1;
    position: static;
  }
}

.layout-ctrl-input {
  display: none;
}
.layout-ctrl-input:checked + .rk-layout-ctrl-cont label {
  display: none;
  color: #a0a0a0;
}
@media (min-width: 64em) {
  .layout-ctrl-input:checked + .rk-layout-ctrl-cont label {
    display: block;
  }
}

.rk-layout-ctrl-mobile {
  color: #a0a0a0;
  height: 19px;
  position: absolute;
  right: 40px;
  top: 46px;
  width: 19px;
  z-index: 400;
}
.top-fixed .rk-layout-ctrl-mobile {
  position: fixed;
}
.rk-layout-ctrl-mobile svg {
  transition:         -webkit-transform 250ms;
  transition:         transform 250ms;
  transition:         transform 250ms, -webkit-transform 250ms;
  position: absolute;
  width: 48%;
}
.rk-layout-ctrl-mobile svg:nth-child(1),
.rk-layout-ctrl-mobile svg:nth-child(2) {
  top: 0;
  -webkit-transform-origin: top center;
          transform-origin: top center;
}
.rk-layout-ctrl-mobile svg:nth-child(1),
.rk-layout-ctrl-mobile svg:nth-child(3) {
  left: 0;
}
.rk-layout-ctrl-mobile svg:nth-child(2),
.rk-layout-ctrl-mobile svg:nth-child(4) {
  right: 0;
}
.rk-layout-ctrl-mobile svg:nth-child(3),
.rk-layout-ctrl-mobile svg:nth-child(4) {
  bottom: 0;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
}
.rk-layout-ctrl-mobile svg:nth-child(1),
.rk-layout-ctrl-mobile svg:nth-child(4) {
  -webkit-transform: scaleY(1.3);
          transform: scaleY(1.3);
}
.rk-layout-ctrl-mobile svg:nth-child(2),
.rk-layout-ctrl-mobile svg:nth-child(3) {
  -webkit-transform: scaleY(.7);
          transform: scaleY(.7);
}
.rk-layout-ctrl-mobile.layout-blog svg:nth-child(1),
.rk-layout-ctrl-mobile.layout-blog svg:nth-child(3) {
  -webkit-transform: scaleX(1.3);
          transform: scaleX(1.3);
  -webkit-transform-origin: left center;
          transform-origin: left center;
}
.rk-layout-ctrl-mobile.layout-blog svg:nth-child(2),
.rk-layout-ctrl-mobile.layout-blog svg:nth-child(4) {
  -webkit-transform: scaleX(1.3);
          transform: scaleX(1.3);
  -webkit-transform-origin: right center;
          transform-origin: right center;
}
#layout-grid:checked ~ .rk-layout-ctrl-mobile svg {
  -webkit-transform: scale(1);
          transform: scale(1);
}
@media (max-width: 40em) {
  .rk-layout-ctrl-mobile {
    display: none;
  }
